import React, { useEffect, useState } from 'react';
import { Form, Input, Modal, Select, Table, message, DatePicker } from 'antd';
import {UnorderedListOutlined, AreaChartOutlined, EditOutlined, DeleteOutlined} from '@ant-design/icons';
import Layout from '../component/layouts/layout';
import axios from 'axios';
import Loading from '../component/loading';
import moment from 'moment';
import Chart from '../component/chart';

const { RangePicker } = DatePicker;

const Homepage = () => {
  const[showModel, setShowmodel]=useState(false);
const [loading,setloading] =useState(false);
const[gTdata,setgTdata]=useState([]);//for getting all transections
const[filter,setfilter]=useState('7');
const[selectdate, setselectdate]=useState([]);
const[type, setType]=useState('all');
const[viewdata,setviewdata]=useState('table');
const[editable,seteditable]=useState(null);


//table data

const column=[
  {
  title:'Date',
  dataIndex:'date',
  render : (text)=><span>{moment(text).format('DD-MM-YYYY')}</span>
},
{
  title:'Amount',
  dataIndex:'amount'
},
{
  title:'Type',
  dataIndex:'type'
},
{
  title:'Category',
  dataIndex:'category'
},
{
  title:'References',
  dataIndex:'reference'
},
{
  title:'Actions',
  render:(text,record)=>(
  <div>
      <EditOutlined onClick={()=>{
        seteditable(record)
        setShowmodel(true)
      }}/>
      <DeleteOutlined className='mx-2'
      onClick={()=>{deletehandler(record)}}
      />
    </div>
   )
}
]



//use effect hook for display data without button
useEffect(()=>{
  //get transection
const gettransections= async()=>{
  try {
    const user=JSON.parse(localStorage.getItem("user"));
    setloading(true);
    const res=await axios.post('/api/v2/transections/get-transection',{userid:user._id,filter,selectdate,type});
    setloading(false);
setgTdata(res.data);
console.log(res.data)
  } catch (error) {
    console.log(error)
    message.error('not fetch data')
  }

}
  gettransections();


},[filter, selectdate, type]);
const gettransections= async()=>{
  try {
    const user=JSON.parse(localStorage.getItem("user"));
    setloading(true);
    const res=await axios.post('/api/v2/transections/get-transection',{userid:user._id,filter,selectdate,type});
    setloading(false);
setgTdata(res.data);
console.log(res.data)
  } catch (error) {
    console.log(error)
    message.error('not fetch data')
  }

}
  // gettransections();








//delete handler
const deletehandler= async(record)=>{
try {
  setloading(true);
  await axios.post('/api/v2/transections/delete-transection',{transectionId:record._id});
  gettransections();
  setloading(false);
  message.success('transection deleted')
} catch (error) {
  console.log(error)
  // res.status(500).json(error)
  message.error('not deleting')
}
}
  //form handling
  const submithandle= async (values)=>{
    // console.log(values);
    try {
      const user=JSON.parse(localStorage.getItem("user"));
      setloading(true)
    if (editable) {
      await axios.post('/api/v2/transections/edit-transection',{payload:{...values, userid:user._id},tnsId:editable._id})
      gettransections();
      setloading(false)
      
      message.success("transection updated successfully");
    }else{
      await axios.post('/api/v2/transections/add-transection',{...values, userid:user._id});
      gettransections();
      setloading(false)
      message.success("transection added successfully");
    }
      setShowmodel(false);
      seteditable(null)
    } catch (error) {
      console.log(error)
      setloading(false)
      message.error('failed to add transection')
    }
   
  }



  
  return (
    <Layout>
      {loading && <Loading/>}
      <div className='filters'>
<div>
  <h6>select filters</h6>
<Select value={filter} onChange={(values)=>setfilter(values)}>
  <Select.Option value='7'>Last 1 Week</Select.Option>
  <Select.Option value='30'>Last 1 month</Select.Option>
  <Select.Option value='365'>Last 1 year</Select.Option>
  <Select.Option value='custom'>Custom</Select.Option>
</Select>
{filter === 'custom' && <RangePicker value={selectdate} onChange={(values)=>{
setselectdate(values)
}}/>}
</div>
<div>
  <h6>select type</h6>
<Select value={type} onChange={(values)=>setType(values)}>
  <Select.Option value='all'>All</Select.Option>
  <Select.Option value='income'>Income</Select.Option>
  <Select.Option value='expense'>Expense</Select.Option>
</Select>
{filter === 'custom' && <RangePicker value={selectdate} onChange={(values)=>{
setselectdate(values)
}}/>}
</div>

<div className='iconswitch'>
<UnorderedListOutlined className={`mx-2 ${viewdata === 'table'?'activeicon':'inactiveicon'}`} onClick={()=>setviewdata('table')}/>
<AreaChartOutlined className={`mx-2 ${viewdata === 'chart'?'activeicon':'inactiveicon'}`} onClick={()=>setviewdata('chart')}/>
  </div>
<div>

  
  <button className='btn btn-primary' onClick={()=> setShowmodel(true)}>add new</button>
  </div>
      </div>
<div className='content'>
{viewdata ==='table'?<Table columns={column} dataSource={gTdata}/>:<Chart alltns={gTdata}/>}

  

</div>
<Modal title={editable?'Edit Transection':'Add Transection'} open={showModel} onCancel={()=>setShowmodel(false)} footer={false}>


<Form layout="verticle" onFinish={submithandle} initialValues={editable} >
<Form.Item label="Amount" name="amount">
  <Input type="text"/>
</Form.Item>
<Form.Item label="Type" name="type">
  <Select>
    <Select.Option value="income">Income</Select.Option>
    <Select.Option value="expense">Expense</Select.Option>
  </Select>
</Form.Item>
<Form.Item label="Category" name="category">
  <Select>
    <Select.Option value="salary">Salary</Select.Option>
    <Select.Option value="tip">Tip</Select.Option>
    <Select.Option value="project">Project</Select.Option>
    <Select.Option value="food">Food</Select.Option>
    <Select.Option value="movies">Movies</Select.Option>
    <Select.Option value="bills">Bills</Select.Option>
    <Select.Option value="medical">Medical</Select.Option>
    <Select.Option value="fee">Fee</Select.Option>
    <Select.Option value="tax">Tax</Select.Option>
  </Select>
</Form.Item>
<Form.Item label="Date" name="date">
    <Input type='date'/>
  </Form.Item>
<Form.Item label="Reference" name="reference">
  <Input type="text"/>
</Form.Item>
<Form.Item label="Description" name="description">
  <Input type="text"/>
</Form.Item>
<div className='d-flex justify-content-end'>
  <button type='submit' className='btn btn-primary' >Save</button>
</div>
</Form>
</Modal>
      </Layout>
  )
}

export default Homepage
