import React,{useState, useEffect} from 'react';
import{ Form, Input, message }from 'antd'
import { Link,useNavigate } from 'react-router-dom';
import Loading from '../component/loading';
import axios from 'axios';


const Login = () => {
    const [loading,setLoading]=useState(false)
        //submit code
        const navigate =useNavigate();
const submitHandle= async (values)=>{
    // console.log(values);
    try {
        setLoading(true);
       const {data}= await axios.post('/api/v2/users/login' , values);
       setLoading(false);
       message.success('login Successfully');
       localStorage.setItem('user', JSON.stringify({...data.oneuser, password:""}))
      
       navigate('/')
    } catch (error) {
        setLoading(false);
     message.error('something went wrong');
    }
}

//prevent for user registration
useEffect(()=>{
    if(localStorage.getItem('user')){
        navigate('/');
    }
},[navigate]);

  return (
    <>
     <div className='register d-flex align-items-center justify-content-center'>
     {loading && <Loading />}
<Form layout="vertical" onFinish={submitHandle}>
    <h1>login form</h1>
    <Form.Item label="Email" name="email">
        <Input type='email'/>
    </Form.Item>
    <Form.Item label="Password" name="password">
        <Input type='password'/>
    </Form.Item>
    <div className='d-flex justify-content-between'>
        <Link to="/register">Not a user? click here to registere</Link>
        <button className='btn btn-primary'>Login</button>
    </div>
</Form>




</div>

    </>
  )
}

export default Login